.roadmapSection {
    min-height: 428px;
    background-color: #27273e;
    position: relative;
    padding-top: 120px;
    display: flex;

    .titleSection {
        margin-left: 180px;

        .title {
            margin-top: 32px;
            font-weight: bold;
            font-size: 48px;
            line-height: 48px;
            letter-spacing: -1px;

            color: white;
        }
    }

    .backButton {
        min-width: 48px;
        height: 48px;
        background: linear-gradient(
            135deg,
            rgba(245, 247, 250, 0.12) 0%,
            rgba(245, 247, 250, 0.06) 51.58%,
            rgba(245, 247, 250, 0.0001) 98.94%
        );
        border: 1px solid rgba(245, 247, 250, 0.06);
        box-sizing: border-box;
        box-shadow: 0px 1px 1px rgba(20, 16, 41, 0.4), -4px -4px 8px rgba(224, 224, 255, 0.04),
            8px 8px 24px rgba(20, 16, 41, 0.4);
        backdrop-filter: blur(108.731px);
        border-radius: 12px;
    }

    .nextButton {
        min-width: 105px;
        margin-left: 16px;
        height: 48px;
        background: linear-gradient(
            135deg,
            rgba(245, 247, 250, 0.12) 0%,
            rgba(245, 247, 250, 0.06) 51.58%,
            rgba(245, 247, 250, 0.0001) 98.94%
        );
        border: 1px solid rgba(245, 247, 250, 0.06);
        box-sizing: border-box;
        box-shadow: 0px 1px 1px rgba(20, 16, 41, 0.4), -4px -4px 8px rgba(224, 224, 255, 0.04),
            8px 8px 24px rgba(20, 16, 41, 0.4);
        backdrop-filter: blur(108.731px);
        border-radius: 12px;
        color: white;
        text-transform: none;
        font-size: 16px;
    }

    .cardList {
        display: flex;
        position: relative;
        margin-left: 200px;
        z-index: 3;

        .card {
            width: 350px;

            .cardDate {
                color: white;
                width: 64px;
                height: 64px;
                background: linear-gradient(135deg, #2d61e5 0%, #8a62f6 53.09%, #e3477e 100%);
                box-shadow: 0px 0px 1px rgba(45, 97, 229, 0.24), 0px 2px 4px -1px rgba(10, 70, 82, 0.12),
                    0px 16px 24px rgba(45, 97, 229, 0.24), 0px 8px 8px -4px rgba(45, 97, 229, 0.12);
                border-radius: 12px;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
            }

            .cardDateText1 {
                font-size: 24px;
            }

            .cardDateText2 {
                font-size: 13px;
                color: rgba(255, 255, 255, 0.8);
            }

            .cardTitle {
                width: 250px;
                margin-top: 24px;
                color: white;
                font-size: 24px;
            }

            .cardText {
                width: 220px;
                margin-top: 16px;
                color: rgba(224, 224, 255, 0.6);
                font-size: 13px;
                line-height: 20px;
                mix-blend-mode: normal;
            }
        }

        .bar {
            position: absolute;
            top: 29px;
            width: 983px;
            height: 6px;
            background: rgba(224, 224, 255, 0.03);
            mix-blend-mode: normal;
            border-radius: 4px;
        }
    }

    .roadmapImage {
        position: absolute;
        background-image: url("../../../assets/Landing/roadmap_image.png");
        width: 850px;
        height: 847px;
        right: 0px;
        top: -300px;
        mix-blend-mode: lighten;
    }

    .roadmapImage2 {
        position: absolute;
        background-image: url("../../../assets/Landing/roadmap_image2.png");
        width: 280px;
        height: 280px;
        bottom: -143px;
        left: 106px;
        z-index: 3;
    }
}
