.aboutSection {
    height: calc(100vw * 0.7);
    background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;

    .title {
        padding-top: 120px;
        font-size: 48px;
        color: white;
        z-index: 3;
    }

    .contentText {
        margin-top: 32px;
        color: rgba(224, 224, 255, 0.6);
        width: 649px;        
        line-height: 28px;
        font-size: 16px;
        text-align: center;
        z-index: 3;
    }
}
