.landingPage {
    position: relative;
    z-index: 3;
    font-family: "Klavika Md" !important;

    .landingVideo {
        position: absolute;
        width: 100%;
        height: 100vh;
        object-fit: fill;
    }

    .content {
        position: relative;
        z-index: 5;
        margin-left: 48px;
        @media screen and (max-width: 768px) {
            margin-left: 24px;
        }
    }

    .text1 {
        font-size: 56px;
        font-weight: bold;
        color: white;
        padding-top: 10%;
        @media screen and (max-width: 768px) {
            font-size: 36px;
            padding-top: 80px;
        }
    }

    .text2 {
        font-size: 24px;
        font-weight: bold;
        color: white;
        text-transform: uppercase;
        margin-top: 16px;
        @media screen and (max-width: 768px) {
            font-size: 20px;
        }
    }

    .text3 {
        font-size: 18px;
        color: white;
        margin-top: 32px;
        width: 500px;
        @media screen and (max-width: 768px) {
            font-size: 16px;
            width: calc(100% - 16px);
        }
    }

    .countdownText {
        font-size: 54px;
        font-family: "Klavika Md";
        color: white;
        margin-top: 56px;

        @media screen and (max-width: 768px) {
            font-size: 36px;
            margin-top: 24px;
            text-align: center;
            margin-right: 32px;
        }
    }

    .cardList {
        display: flex;
        margin-top: 30px;
        @media screen and (max-width: 768px) {
            justify-content: center;
            margin-right: 16px;
            margin-top: 20px;
        }
    }

    .card {
        margin-right: 24px;
        @media screen and (max-width: 768px) {
            margin-right: 12px;
        }

        .cardText1 {
            font-size: 42px;
            color: white;
            text-align: center;
            padding-top: 18px;
            font-family: "Klavika Md";
            @media screen and (max-width: 768px) {
                font-size: 28px;
                padding-top: 20px;
            }
        }

        .cardText2 {
            font-size: 17px;
            color: rgba(224, 224, 255, 0.6);
            text-align: center;
            margin-top: 8px;
            font-family: "Klavika Md";
            @media screen and (max-width: 768px) {
                font-size: 15px;
                margin-top: 24px;
                margin-bottom: 24px;;
            }
        }
    }

    .buttonGroup {
        display: flex;
        flex-direction: row;
        margin-top: 32px;

        @media screen and (max-width: 768px) {
            flex-direction: column;
        }
    }

    .buttonLink {
        text-decoration: none;
    }

    .button {
        padding: 16px 40px;
        background: #ffffff;
        border-radius: 8px;
        font-weight: bold;
        font-size: 20px;
        line-height: 24px;
        color: #050625;
        margin-right: 16px;
        font-family: "Klavika Md";
        text-transform: none;
        width: 250px;
        @media screen and (max-width: 768px) {
            font-size: 18px;
            line-height: 20px;
            width: calc(100% - 24px);
            margin-bottom: 12px;
        }
    }

    .button:hover {
        background-color: rgba(255, 255, 255, 0.7) !important;
    }
}
